import React from 'react';
import { Input } from "antd";
import {
  createPortalCategory,
  deletePortalCategory,
  getPortalCategories,
  updatePortalCategory,
} from '../../../services/portatCateogry';
import { requiredRule } from '../../../utils/validateRules';
import CrudPage from '../../../components/common/CrudPage'

export default () => {
  const model = [
    { field: 'id', name: 'Id' },
    { field: 'label', name: 'Tên', rules: [requiredRule], formItem: <Input /> },
  ];

  const config = {
    title: 'Chuyên mục',
    serviceGetList: getPortalCategories,
    serviceCreate: createPortalCategory,
    serviceUpdate: updatePortalCategory,
    serviceDelete: deletePortalCategory,
    tableColumns: ['label'],
    saveFormItems: ['label'],
    searchFormItems: ['label'],
  };
  return <CrudPage model={model} config={config} />
};



