import { request } from '../utils/requestToJs';

const prefix = '/api/education';

export const getPortalCategories = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/PortalCategory`,
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createPortalCategory = (data) =>
  request({
    prefix,
    url: `/PortalCategory`,
    method: 'POST',
    params: {},
    data,
  });

export const updatePortalCategory = (data) =>
  request({
    prefix,
    url: `/PortalCategory/${data.id}`,
    method: 'PUT',
    params: {},
    data,
  });

export const deletePortalCategory = (id) =>
  request({
    prefix,
    url: `/PortalCategory/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



