import React, { useEffect } from 'react';
import { Form, Row } from 'antd';
import Btn from 'components/common/Btn/Btn';
import { getQueryParams, updateQueryParams } from '../../../utils/urlUtil';

const FormSearch = ({ form, FormItems }) => {
  const { validateFields } = form;

  useEffect(() => {
    form.setFieldsValue(getQueryParams());
  }, []);

  const handleSearch = async e => {
    e.preventDefault();
    validateFields(async (err, values) => {
      if (err) return;
      updateQueryParams(values);
    });
  };

  return (
    <Form colon={false}>
      <Row gutter={[12, 12]}>
        <FormItems form={form} />
        <Btn style={{ marginTop: 48, marginLeft: 5 }} primary onClick={handleSearch}>
          Tìm kiếm
        </Btn>
      </Row>
    </Form>
  );
};

export default Form.create()(FormSearch);



