import React, { useState } from 'react';
import { Modal } from 'antd';
import MainForm from './MainForm';
import './styles.scss';

export default props => {
  const [visible, setVisible] = useState(false);
  const [initValue, setInitValue] = useState({});
  const show = event => {
    if (event.initValue) {
      setInitValue(event.initValue);
    } else {
      setInitValue(props.initValue);
    }
    setVisible(true);
  };

  const hide = () => {
    setVisible(false);
  };

  const comp = () => (
    <Modal
      className="comp-FormModal"
      width={620}
      maskClosable={false}
      closable={false}
      footer={null}
      title={props.title}
      visible={visible}
    >
      <MainForm {...props} modal={{ visible, initValue, hide, show }} />
    </Modal>
  );

  return { Modal: comp, show, hidden: hide };
};



