import React, { useEffect, useState } from 'react';
import { Row, Col, message, Form } from 'antd';
import Card from '../../../components/common/Card/index';
import CardTitle from '../../../components/common/CardTitle/index';
import TableData from '../../../react-uikit/component/table/TableData';
import BtnGroup from '../../../components/common/Btn/BtnGroup';
import Btn from '../../../components/common/Btn/Btn';
import BtnDeleteConfirm from '../../../components/common/Btn/BtnDeleteConfirm';
import useFormModal from '../../../components/common/FormModal/useFormModal';
import FormSearch from './FormSearch';
import { getQueryParams } from '../../../utils/urlUtil';

export default props => {
  const { model, config } = props;
  // component
  const modelObject = model.reduce((obj, item) => {
    obj[item.field] = item;
    return obj;
  }, {});
  const [reload, setReload] = useState(0);
  const [paramSearch, setParamSearch] = useState({});
  const queryParam = getQueryParams();
  const handleChangeQueryParams = () => {
    setParamSearch(queryParam);
  };
  useEffect(() => {
    handleChangeQueryParams();
  }, [props]);

  const SearchFormItems = ({ form }) => {
    const { getFieldDecorator } = form;
    return (
      <div>
        {config.searchFormItems.map(item => {
          if (typeof item === 'string' && modelObject[item]) {
            const modelField = modelObject[item];
            return (
              <Col span={6}>
                <Form.Item label={modelField.name}>
                  {getFieldDecorator(item, {
                    rules: [],
                  })(modelField.formItem)}
                </Form.Item>
              </Col>
            );
          }
          return <Col span={6}>{item({ form })}</Col>;
        })}
      </div>
    );
  };
  const FormSave = useFormModal({
    title: config.title,
    FormItems: ({ form, initValue }) => {
      const { getFieldDecorator } = form;
      const { FORM_MODE } = initValue;
      return (
        <div>
          {config.saveFormItems.map(item => {
            if (typeof item === 'string' && modelObject[item]) {
              const modelField = modelObject[item];
              let { formItem } = modelField;
              if (FORM_MODE === 'EDIT' && modelField.editFormItem) {
                formItem = modelField.editFormItem;
              }
              if (FORM_MODE === 'NEW' && modelField.newFormItem) {
                formItem = modelField.newFormItem;
              }
              if (!formItem) {
                return null;
              }
              return (
                <Form.Item label={modelField.name} className="m-0">
                  {getFieldDecorator(item, {
                    rules: modelField.rules || [],
                  })(formItem)}
                </Form.Item>
              );
            }
            return item;
          })}
        </div>
      );
    },
    onSubmit: async values => {
      const { FORM_MODE } = values;
      if (config.saveFormCustomValue) {
        // eslint-disable-next-line no-param-reassign
        values = config.saveFormCustomValue(values);
      }
      if (FORM_MODE === 'EDIT') {
        await config.serviceUpdate(values);
        message.success('Cập nhật thành công');
      } else {
        await config.serviceCreate(values);
        message.success('Thêm thành công');
      }
      if (config.saveFormOnSuccess) {
        // eslint-disable-next-line no-param-reassign
        config.saveFormOnSuccess();
      }
      setReload(reload + 1);
    },
  });
  const tableColumns = config.tableColumns.map(item => {
    if (typeof item === 'string' && modelObject[item]) {
      const modelField = modelObject[item];
      return {
        title: modelField.name,
        dataIndex: item,
        key: item,
      };
    }
    return item;
  });
  const handleDelete = async record => {
    await config.serviceDelete(record.id);
    setReload(reload + 1);
    message.success('Xóa thành công');
  };
  const actionColumns = {
    title: '',
    dataIndex: 'action',
    key: 'action',
    width: 60,
    render: (text, record) => {
      const btnEditProps = config.btnEdit ? config.btnEdit(record).props : {};
      return (
        <BtnGroup>
          {config.serviceUpdate && (
            <Btn
              {...(btnEditProps || {})}
              primary
              onClick={() =>
                FormSave.show({
                  initValue: { ...record, FORM_MODE: 'EDIT' },
                })
              }
            >
              Sửa
            </Btn>
          )}
          {config.serviceDelete && <BtnDeleteConfirm onConfirm={() => handleDelete(record)} />}
        </BtnGroup>
      );
    },
  };
  tableColumns.push(actionColumns);
  const btnAddProps = config.btnAdd ? config.btnAdd.props : {};
  return (
    <Card>
      <CardTitle title={config.title} />
      <div>
        <FormSearch FormItems={SearchFormItems} />
      </div>
      <Row>
        <Col span={24}>
          <div style={{ float: 'right' }}>
            <Btn
              {...(btnAddProps || {})}
              primary
              onClick={() => {
                FormSave.show({
                  initValue: { FORM_MODE: 'NEW' },
                });
              }}
            >
              Thêm
            </Btn>
          </div>
        </Col>
      </Row>
      <TableData
        reload={reload}
        paramSearch={paramSearch}
        columns={tableColumns}
        service={config.serviceGetList}
      />
      <FormSave.Modal />
    </Card>
  );
};



