import React, { Fragment, useEffect, useState } from 'react';
import { Form } from 'antd';
import Btn from '../Btn/Btn';
import BtnGroup from '../Btn/BtnGroup';

const MainForm = Form.create()(({ form, modal, FormItems, onSubmit, props }) => {
  const { validateFields, resetFields } = form;
  const [isSubmitting, setIsSubmitting] = useState(false);
  const handleSubmit = async e => {
    e.preventDefault();
    validateFields(async (err, values) => {
      if (err) return;
      setIsSubmitting(true);
      if (onSubmit) {
        try {
          await onSubmit({ ...modal.initValue, ...values }, { initValue: modal.initValue });
          modal.hide();
        } catch (errApi) {
          console.error(errApi)
          setIsSubmitting(false);
        }
      }
      setIsSubmitting(false);
    });
  };

  const handleInitValue = () => {
    form.setFieldsValue(modal.initValue);
  };

  const handleCancel = () => {
    modal.hide();
  };

  useEffect(() => {
    if (modal.visible) {
      handleInitValue();
    } else {
      resetFields();
    }
  }, [modal.visible]);

  return (
    <Fragment>
      <Form colon={false}>
        <FormItems form={form} props={props} initValue={modal.initValue} />
        <BtnGroup>
          <Btn primary onClick={handleSubmit}>
            Lưu
          </Btn>
          <Btn disabled={isSubmitting} onClick={handleCancel}>
            Hủy
          </Btn>
        </BtnGroup>
      </Form>
    </Fragment>
  );
});

export default MainForm;



