import React, { Component } from 'react';
import { Popconfirm } from 'antd';
import Btn from './Btn'
import './style.scss';

export default class BtnDeleteConfirm extends Component {
  states = '';

  render() {
    return (
      <Popconfirm okText="Xóa" cancelText="Hủy" title="Xác Nhận Xóa ?" {...this.props}>
        <Btn primary style={{  ...(this.props.styleBtn || {}) }}>Xóa</Btn>
      </Popconfirm>
    );
  }
}

BtnDeleteConfirm.defaultProps = {
  className: '',
  title: 'Xác Nhận Xóa ?',
};



